import { h, Component } from "preact"
import Axios from "axios";
import { Image } from "./image";

export class Galleries extends Component {

  constructor() {
    super();
    this.state = {
      galleries: []
    };
  }


  // Lifecycle: Called whenever our component is created
  componentDidMount() {
    const galleries = []
    const random = (min, max) => {
      return Math.round(Math.random() * (max - min) + min)
    }

    for (let index = 0; index < 25; index++) {
      const size = { width: random(200, 400), height: random(200, 400) }
      galleries.push({ url: `https://placekitten.com/${size.width}/${size.height}` })
    }

    this.setState({ galleries })
  }

  // Lifecycle: Called just before our component will be destroyed
  componentWillUnmount() {
  }

  render() {
    const renderedDOM = this.state.galleries.map((gallery, index) => (
        <Image gallery={gallery} />
    ))

    return <div class='galleries-wrap'>{renderedDOM}</div>
  }
}